.shippingaddress {
    display: flex;
    flex-direction: column;
    border: none;
    outline: none;
    background: #FFFFFF;
    padding: 70px 0;
    display: flex;
    flex-direction: column;
    /* text-align: center; */
    gap: 20px;
    align-items: center;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 0.8rem;
    font-weight: 500;

    color: #C1C8E1;
}

