.shippingaddress_button button {
    background: #FB2E86;
    border-radius: 2px;
    border: none;
    outline: none;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 0.9rem;
    font-weight: 600;
    padding: 15px;
    color: #FFFFFF;
    width: 182px;
    /* height: 44.06px; */

}

.shippingaddress_button {
    padding-top: 40px;
}




