@media only screen and (max-width: 500px) {
    /* .shippingaddress {
        padding-left: 0%;
        margin: 0;
    }

    .shippingaddress_body {
        width: 100%;
    } */
    .shippingaddress {
        display: block;
    }

    .shippingaddress_header {
        display: contents;
    }

    .shippingaddress_body {
        padding-top: 17px;
        display: flex;
        width: 100%;
    }
    .shippingaddress_button{
        display: flex;
        justify-content: center;
    }
}

@media only screen and (max-width: 350px) {
    .shippingaddress {
        display: block;
    }

    .shippingaddress_header {
        display: contents;
    }

    .shippingaddress_body {
        padding-top: 17px;
        
    }
    .shippingaddress_button{
        display: flex;
        justify-content: center;
    }
}