.mobmenubar {
    background-color: white;
    /* padding: 0 5%; */
    width: 98%;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 0.8rem;
    font-weight: 500;
    color: black;
    margin: 0px;
    padding: 0px;
    /* position: absolute; */
    margin-top: -160px;
    /* position: sticky; */
    /* bottom: 0px; */
    z-index: 1;
  }
  
  .mobmenubar_ul {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  
  .mobmenubar_li {
    list-style-type: none;
    width: 100%;
    font-weight: bold;
    color: rgb(12, 11, 11);
    /* border-bottom: 1px solid black; */
   padding: 5px; 
  }
  .mobmenubar_li_cat{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
       padding: 5px 10px; 
       color: black;
  }
  
  /* .mobmenubar_li:hover {
    color: #fb2e86;
    display: flex;
    border-bottom: 3px solid #fb2e86;
  } */
  
  .mobmenubar_popup {
    position: absolute;
    display: flex;
    background-color: white;
    width: 100%;
    margin-left: 2px;
    top: 0;
    border: 1px solid gray;
    margin-top: -325px;
    z-index: 1;
    /* overflow-y: scroll; */
  }
  .mobmenubar_popup_popup {
    /* position: absolute; */
    display: flex;
    background-color: white;
    width: 101%;
    margin-left: 2px;
    top: 0;
    border: 1px solid gray;
    margin-top: -325px;
    z-index: 1;
    /* overflow-y: scroll; */
    /* height: 74vh; */
  }
  
  .mobmenubar_title {
    width: 20%;
    color: black;
    /* padding: 10px; */
    /* justify-content: center;
    display: flex; */
    /* flex-direction: row; */
  }
  .mobmenubar_li_cat_arrow{
    width: 80%;
    display: flex;
    justify-content: right;

      }
  .mobmenubar_li_cat_arrow_inn{
   
  }
  .mobmenubar_li:hover~.menubar_popup {
    display: flex;
  }
  
  .mobmenubar_title:hover~.menubar_popup {
    display: flex;
  }
  
  .mobmenubar_title_goldrate {
    display: flex;
    flex-direction: column;
    margin-top: 8.5px;
    z-index: 1;
    /* position: absolute; */
    background-color: #f4579b;
    /* width: 50%; */
    padding-top: 20px;
    padding-bottom: 30px;
    border: 1px solid silver;
    /* margin-left: -2.5%; */
    row-gap: 20px;
    justify-content: center;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: .85rem;
  
  }
  
  .mobmenubar_title_goldrate label {
    /* display: flex; */
    /* margin-left: 50px; */
    display: flex;
    justify-content: center;
  }
  
