    margin-top: 35.7px;
    margin-left: 30px;
    color: white;
    justify-content: center;
  } */
  
  .mobmenubar_title_goldrate_row1 {
    display: flex;
    justify-content: center;
    border-bottom: 2px solid rgb(207, 201, 168);
    width: 85%;
    margin-left: 15px;
    padding-bottom: 10px;
  }
  
  .mobmenubar_title_goldrate_row1 span {
    padding-left: 10px;
    padding-right: 10px;
  }
  
  .mobmenubar_popup_left {
    width: 100%;
    text-align: left;
    overflow-y: scroll;
  }
  
  .mobmenubar_popup_left ul {
    padding: 0;
    display: flex;
    justify-content: left;
    flex-direction: column;
    margin: 0;
    overflow-y: scroll;
    /* margin-top: 160px; */
  }
  .mobmenubar_popup_left_pupup{
    width: 100%;
    padding: 0;
    display: flex;
    justify-content: left;
    flex-direction: column;
    margin: 0;
    overflow-y: scroll;
    margin-top: 160px;
  }

  
  .mobmenubar_popup_left_li {
    /* overflow-y: scroll; */
    list-style-type: none;
  }
  .mobmenubar_popup_left_li_inner{
    border-bottom: 1px solid rgb(210, 207, 207);
    padding: 10px;
    background-color: white;
    justify-content: space-between;
    color: black;
    align-items: center;
    font-weight: normal;
    display: flex;
    /* margin-left: -30px; */
  }
  .mobmenubar_popup_left_li_inner_popup{
    border-bottom: 1px solid rgb(210, 207, 207);
    padding: 10px;
    background-color: white;
    justify-content: space-between;
    color: black;
    align-items: center;
    font-weight: normal;
    display: flex;
    margin-left: -30px;
  }
  .mobmenubar_popup_left_category_popup{
    border-bottom: 1px solid rgb(210, 207, 207);
    padding: 10px;
    background-color: white;
    color: black;
    align-items: center;
    font-weight: normal;
    display: flex;
    margin-left: -45px;
  }
  .mobmenubar_popup_left_li_inner1{
    border-bottom: 1px solid rgb(210, 207, 207);
    /* position: absolute; */
    /* z-index: 2; */
    width: 100%;
  }
  .mobmenubar_popup_left_category{
    border-bottom: 1px solid rgb(210, 207, 207);
    padding: 10px;
    background-color: white;
    color: black;
    align-items: center;
    font-weight: normal;
    display: flex;
    margin-left: -10px;
  }
  .mobmenubar_popup_right_one li{
    border-bottom: .1px solid rgb(237, 233, 233);
    margin-left: 15px;
  }
  .mobmenubar_popup_left_category label{
    color: #f4579b;
    font-weight: 550;
    font-size: .95rem;
  }
  .mobmenubar_popup_left_category_popup label{
    color: #f4579b;
    font-weight: 550;
    font-size: .95rem;
  }
  .mobmenubar_popup_left_title {
    display: flex;
    flex-direction: column;
  }
  
  .mobmenubar_popup_left_title_bold {
    display: flex;
    flex-direction: column;
    font-weight: bold;
  }
  
  .mobmenubar_popup_left_img img {
    width: 30px;
    height: 30px;
  }
  
  .mobmenubar_popup_right_one_img img {
    width: 30px;
    height: 30px;
  }
  
  
  .mobmenubar_popup_left_li:hover {
    font-weight: bold;
  }
  
  .mobmenubar_popup_right {
    display: flex;
    flex-direction: column;
    width: 100%;
    /* margin-left: -2.5px; */
    /* position: absolute; */
    background-color: white;
    margin-top: 15px;
    /* z-index: 5; */
    /* justify-content: right; */
  }
  .mobmenubar_popup_right_subcat{
    display: flex;
    align-items: center;
    padding-bottom: 10px;
    padding-top: 10px;
    border-bottom: 1px solid rgb(243, 235, 235);
  }
  .mobmenubar_popup_right_subcat label{
    color: #f4579b;
    font-weight: 550;
    font-size: .95rem;
  }
  
  .mobmenubar_popup_right h3 {
    color: rgb(161, 160, 160);
    font-size: 0.9rem;
  }
  
  .mobmenubar_popup_right ul {
    padding: 0;
    margin: 0;
  }
  
  .mobmenubar_popup_right li {
    color: black;
    padding: 10px 0;
    font-weight: normal;
    display: flex;
    align-items: center;
  }
  
  .mobmenubar_popup_right li:hover {
    font-weight: bold;
  }
  
  .mobmenubar_popup_icon {
    margin-right: 10px;
  }
  .mobmenubar_popup_right_outer{
    /* overflow-y: scroll;
    height: 25vh; */
    /* position: absolute; */
    background-color: white;
    width: 97%;
    margin-top: 40px;
    /* z-index: 5; */
    /* margin-left: -25px; */
  }
