  .feturedlist_cards{
    width:50%;
    
    /* grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto; */
    /* justify-content:center; */
  }
  .featurelist_outer{
    margin-left:-10px
  }
  /* .featuredlist_outer_inner{
