.Header {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 0.8rem;
  font-weight: 500;
  position: sticky;
  top: 0;
  z-index: 2;
  background-color: #c90053cf;
  /* overflow: hidden; */
}
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.Header_r1 {
  background-color: #c90053cf;
  padding: 10px 5% 0px 5%;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0;
  justify-content: space-between;
}

.Header_r1_left {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: right;
  padding-top: 5px;
}
.Header_r2_col1_logo{
  margin-top: -20px;
}
/* .Header_r2_col1_logo img{
  width:250px;
} */

.Header_r1_left_mailid {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: white;
}
.Header_r1_left_mailid_img{
  padding-top:5px;
}

.Header_r1_left_mailid_mail {
  /* padding-left: 10px; */
  display: flex;
  flex-direction: row;
  align-items: center;
}
.Header_r1_left_contact_img{
  padding-top: 5px;
  color: white;
  font-size: 15px;
}
.Header_r1_left_contact {
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 8px;
  padding-left: 50px;
  padding-bottom: 15px;
}

.Header_r1_left_contact_number {
  /* padding-left: 10px; */
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Header_r1_left_contact_number label {
  color: white;
}

.Header_r1_right {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 45px;
  align-items: center;
}

.Header_r1_right_1 select {
  background-color: #dd2672cf;
  border: none;
  color: white;
}

.Header_r1_right_2 select {
  background-color: #dd2672cf;
  border: none;
  color: white;
}


.Header_r1_right_3 {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}

.Header_r1_right_3_img{
  display: flex;
  justify-content: center;
}
.Header_r1_right_4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* width: 100%; */
  /* background-color: yellow; */
}
.Header_r1_right_4_img{
  display: flex;
  justify-content: center;
}
.Header_r1_right_5_span{
  display: flex;
  justify-content: center;
}

.Header_r1_right_4_wishlist label {
  color: white;
  padding-right: 5px;
}

.Header_r1_right_5 {
  display: flex;
  flex-direction: column;
  color: white;
}

.Header_r1_right_5_count {
  color: white;
  font-weight: 550;
  padding-left: 5px;
  position: absolute;
  /* background-color: rgb(162, 18, 18); */
  width: 16;
  height: 16;
  border-radius: 50%;
  /* padding-right: 10; */
  /* padding-top: +10; */
  margin-left: 15px;
  margin-top: -10px;
  align-items: center;
  justify-content: right;
  display: flex;
}

.Header_r2 {
  background-color: #c90053cf;
  display: flex;
  flex-direction: row;
  padding: 0px 5% 10px 5%;
  justify-content: space-between;
  align-items: center;
}

.Header_r2_col1 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  color: white;
  align-items: center;
  width: 20%;
}

.Header_r2_col1 label {
  display: flex;
  flex-direction: row;
  /* font-weight: bold;
  font-family: Josefin Sans; */
  font-size: 2rem;
  /* text-shadow: 1px 1px; */
}

.Header_r2_col2_outer_inner{
width: 150%;
display: flex;
justify-content: center;
}

.Header_r2_col2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  color: white;
  width: 50%;
  padding-top: 15px;
}

.Header_r2_col2 li {
  padding-left: 25px;
  padding-right: 25px;
  margin: 0;
  font-size: 0.9rem;
  list-style-type: none;
  color: white;
}

.Header_r2_col2 li:hover {
  font-weight: bold;
  color: #7e33e0;
}

.Header_r2_col2 select {
  border: none;
  color: rgb(214, 50, 78);
}

.Header_r2_col2 button {
  border: none;
  background-color: white;
}

.Header_r2_col3 {
  display: none;
  /* display: flex; */
  flex-direction: row;
  justify-content: flex-end;
}

.Header_r2_col3 div {
  background-color: #fb2e86;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Header_r2_col3 input {
  border-width: 1px;
  border-color: #fb2e86;
  border-style: solid;
  padding: 5px 10px;
  outline: none;
}

.Header_r2_col3 img {
  padding: 0 8px;
}

.header_mobview,
.header_mobview1,
.header_mobview_outer {
  display: none;
  align-items: center;
  padding-left: 10px;
}




