.LatestProducts_imageicons1 {
  background-color: #eeeffb;
  width: 35px;
  height: 35px;
  border-radius: 15px;
  margin-left: 15px;
  margin-bottom: 10px;
  margin-top: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.LatestProducts_imageicons1 img {
  width: 30px;

  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-left: 5px; */
  margin-bottom: 10px;
  margin-right: 10px;
  margin-top: 10px;
}

