  justify-content: left;
  color: #fb2e86;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: .9rem;
  font-weight: 500;
  /* line-height: 18.75px; */
  border-bottom: 2px solid #eeeffb;
  transform: matrix(1, 0.01, -0.01, 1, 0, 0);
}

.LatestProducts_bottom_imgchair_righttext_row2 {
  width: 50%;
  display: flex;
  justify-content: right;
}
.LatestProducts_bottom_imgchair_lefttext_row2_lab{
  font-size: .8rem;
  padding-top: 5px;
}
.LatestProducts_bottom_imgchair_righttext_row2 label {
  padding-left: 10px;
  color: #151875;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 0.8rem;
  font-weight: 500;
  line-height: 16.41px;
}

.LatestProducts_bottom_imgchair_righttext_row2 span {
  /* padding-left: 10px; */
  color: red;
  font-family: "Josefin Sans", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  text-decoration-line: line-through;
  padding-left: 10px;
}

